@import url('./normalize.css');
@import url('./fonts/fonts.css');

.app {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  background-color: #b9c;
  background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11 18c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm48 25c3.866 0 7-3.134 7-7s-3.134-7-7-7-7 3.134-7 7 3.134 7 7 7zm-43-7c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm63 31c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM34 90c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zm56-76c1.657 0 3-1.343 3-3s-1.343-3-3-3-3 1.343-3 3 1.343 3 3 3zM12 86c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm28-65c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm23-11c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-6 60c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm29 22c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zM32 63c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm57-13c2.76 0 5-2.24 5-5s-2.24-5-5-5-5 2.24-5 5 2.24 5 5 5zm-9-21c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM60 91c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM35 41c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2zM12 60c1.105 0 2-.895 2-2s-.895-2-2-2-2 .895-2 2 .895 2 2 2z' fill='%23ffffff' fill-opacity='0.74' fill-rule='evenodd'/%3E%3C/svg%3E");
  font-family: 'Rubik', 'Arial Bold', sans-serif;
  color: #600;
}

.header {
  background-color: #97a;
  margin: 0;
  padding: 20px 50px;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.header__heading {
  margin: 0 0 0 20px;
  padding: 0;
  font-size: 40px;
  font-weight: 400;
  text-align: center;
  max-width: 600px;
}

.header__pic {
  width: 100px;
  height: 100px;
  margin: 0;
  padding: 0;
  border: #600 2px double;
  border-radius: 50%;
  animation: spin ease infinite 15s alternate;
}

.footer {
  background-color: #97a;
  margin: 0;
  margin-top: auto;
  padding: 0 50px;
  box-sizing: border-box;
  width: 100%;
  height: fit-content;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.footer__text {
  margin: 0;
  padding: 5px;
  color: #425;
  font-family: 'Neucha', 'Arial Narrow', sans-serif;
  text-decoration: none;
}

.footer__link:hover {
  text-decoration: underline;
}

.main {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
 }

.tools {
  margin: 0 auto auto;
  padding: 20px 50px;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  color: #425;
  font-family: 'Neucha', 'Arial Narrow', sans-serif;
}

.button {
  margin: 5px;
  padding: 10px;
  border-radius: 10px;
  border: #425 3px double;
  font-family: 'Neucha', 'Arial Narrow', sans-serif;
  color: #425;
  background-color: rgba(255, 255, 255, 0.4);
  line-height: 1;
}

.button_switch {
  padding: 0;
  height: fit-content;
  position: relative;
}

.button:hover {
  background-color: rgba(255, 255, 255, 0.8);
}

.header__caution {
  background-color: rgba(255, 255, 255, 0.8);
  position: absolute;
  right: 25px;
  bottom: -40px;
  width: max-content;
  max-width: calc(100vw - 130px);
  display: none;
}

.button_switch:hover > .header__caution {
  display: flex;
}

.header__language {
  line-height: 1;
  padding: 5px;
  margin: 0;
  border-radius: 8px;
  color: #600;
}

.header__language_active {
  background-color: rgba(102, 0, 0, 0.8);
  color: #fff;
}

.generate {
  border: #600 4px double;
}

.themes__button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-items: center;
}

.themes__button_true {
  color: #090;
}

.themes__button_false {
  color: #900;
}


.themes__text {
  margin: 0 20px 0 0;
  padding: 0;
  color: #425;
}

.bag {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  background-color: rgba(153, 119, 170, 0.8);
  margin: 10px;
  padding: 0;
  box-sizing: border-box;
  width: 280px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  border: rgba(68, 34, 85) 3px double;
  border-radius: 10px;
}

.card__header {
  background-color: rgba(153, 119, 170);
  margin: 0;
  padding: 10px;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.card__heading {
  margin: auto 0;
  padding: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 1;
  color: #fff;
}

.card__title {
  margin: 0;
  padding: 10px;
  font-size: 20px;
  line-height: 1;
  color: #536;
}

.card__theme {
  margin: 0;
  padding: 10px;
  font-size: 24px;
  line-height: 1;
  font-family: 'Neucha', 'Arial Narrow', sans-serif;
}

.card__text {
  margin: 0;
  padding: 10px;
  font-size: 18px;
  line-height: 1;
  font-family: 'Neucha', 'Arial Narrow', sans-serif;
  color: #203;
}

@keyframes spin {
  from {
    transform: rotate(-60deg);
  }
  to {
    transform: rotate(60deg);
  }
}

@media screen and (min-width: 1400px) {
  .header, .main, .footer {
    padding-right: calc(50vw - 700px);
    padding-left: calc(50vw - 700px);
  }
}

@media screen and (max-width: 640px) {
  .card {
    width: 100%;
  }

 .header__heading {
  font-size: calc(40px - (640px - 100vw)/15);
 }
}

@media screen and (max-width: 575px) {
  .generate {
    line-height: 2;
  }
}

@media screen and (max-width: 500px) {
  .tools {
    max-width: 360px;
    flex-wrap: wrap;
    padding: 10px 20px;
  }
}